// ---------------------------------------------------------
// Accessibility
//
// A hidden screen reader for readers, focus elements for
// vision impaired and other useful a11y CSS hacks.
// ---------------------------------------------------------

// Variables
$focus-background-color: $color-cobalt-blue;
$focus-outline-color: $color-cobalt-blue;
$focus-text-color: #000;
$tld-mobile: '192.168.196.2:3000';
$tld-browsersync: 'localhost:3000';
$tld-development: 'handbook.test';
$tld-staging: 'handbook.vaiheessa.fi';
$tld-production: 'handbook.dude.fi';

// Text meant only for screen readers.
.screen-reader-text {
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute !important;
  width: 1px;
  word-wrap: normal !important; // Many screen reader and browser combinations announce broken words as they would appear visually.

  &.skip-link {
    margin: 5px;
    color: $color-white !important;
  }

  &:focus {
    background-color: #f1f1f1;
    border-radius: 0;
    box-shadow: 0 0 2px 2px rgba(#000, .6);
    clip: auto !important;
    clip-path: none;
    color: #21759b;
    display: block;
    font-size: 1.7rem;
    font-weight: 700;
    height: auto;
    left: 5px;
    line-height: normal;
    padding: 15px 23px 14px;
    text-decoration: none;
    top: 5px;
    width: auto;
    z-index: 100000; // Above WP toolbar.
  }
}

// Do not show the outline on the skip link target.
[tabindex="-1"]:focus {
  outline: 0;
}

// Suppress focus ring on for mouse users for visual reasons
[data-whatinput="mouse"] *:focus {
  outline: none;
}

// Visually distinct focus color on keyboard
[data-whatinput="keyboard"] {
  input:focus,
  button:focus,
  select:focus,
  textarea:focus {
    outline: 4px solid $focus-outline-color;
    opacity: 1;
  }

  nav ul li a:focus {
    background: $focus-background-color;
    color: $focus-text-color;
  }

  a:focus {
    outline: 2px solid $focus-outline-color;
    background: $focus-background-color;
    opacity: 1;
  }

  a.global-link:focus {
    background: rgba($focus-background-color, .5);
  }
}

// Make theme keyboard accessible with no Javascript enabled
.no-js *:focus {
  outline: 4px solid $focus-outline-color;
}

// Links within content must be underlined
// @link https://make.wordpress.org/themes/handbook/review/accessibility/required/#content-links
.container a,
.entry-content a {
  text-decoration: underline;

  &:hover,
  &:focus {
    text-decoration: none;
  }
}

// Add icon to external links
a[href*="//"]:not([href*="#{$tld-production}"]):not([href*="#{$tld-staging}"]):not([href*="#{$tld-development}"]):not([href*="#{$tld-browsersync}"]):not([href*="#{$tld-mobile}"]):not(.github):not(.no-external-link-indicator) {
  background-image: url('../svg/external-link.svg');
  background-position: right center;
  background-repeat: no-repeat;
  padding-right: 2.5rem;
  margin-right: 1rem;

  @media (max-width: 600px) {
    background-size: 14px 14px;
  }
}

// Light icons for dark navigation
.menu-item a[href*="//"]:not([href*="#{$tld-production}"]):not([href*="#{$tld-staging}"]):not([href*="#{$tld-development}"]):not([href*="#{$tld-browsersync}"]):not([href*="#{$tld-mobile}"]):not(.github):not(.no-external-link-indicator) {
  background-image: url('../svg/external-link-white.svg');
}

// Do not use email links as external links
a[href^="mailto"] {
  background-image: none;
  padding-right: 0;
}

// Footer exceptions
.block-footer .menu-item a[href*="//"]:not([href*="#{$tld-production}"]):not([href*="#{$tld-staging}"]):not([href*="#{$tld-development}"]):not([href*="#{$tld-browsersync}"]),
.block-footer a[href*="//"]:not([href*="#{$tld-production}"]):not([href*="#{$tld-staging}"]):not([href*="#{$tld-development}"]):not([href*="#{$tld-browsersync}"]):not([href*="#{$tld-mobile}"]):not(.github):not(.no-external-link-indicator) {
  background-size: 12px 12px;
  padding-right: 2rem;
}
